body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  min-height: 100vh;
}

.status {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  padding-bottom: 10px;
  border-bottom: 1px solid black;
}

.budget {
  background-color: rgb(91, 206, 91);
  padding: 15px;
  border-radius: 10px;
  color: #fff;
}

.remaining {
  background-color: rgb(218, 201, 110);
  padding: 15px;
  border-radius: 10px;
  color: #fff;
}

.spent {
  background-color: rgb(207, 46, 18);
  padding: 15px;
  border-radius: 10px;
  color: #fff;
}

.chart {
  height: 250px;
  width: 250px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.search {
  background-color: rgb(221, 215, 215);
  padding: 10px;
  border-radius: 10px;
}

.search input {
  width: 100%;
  background-color: rgb(221, 215, 215);
  border: none;
}
.search input:focus {
  outline: none;
}

.transactions {
  padding-bottom: 10px;
  border-bottom: 1px solid black;
}

.transaction {
  background-color: burlywood;
  color: #fff;
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.delete {
  padding-left: 5px;
}

.delete:hover {
  cursor: pointer;
  transform: scale(1.3);
  color: black;
}

.expense {
  background-color: rgb(207, 46, 18);
  color: #fff;
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.income {
  background-color: rgb(91, 206, 91);
  color: #fff;
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.transaction-cost {
  display: flex;
  align-items: center;
}

.add-transaction {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}

.add-label {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.add-label input select {
  border: none;
}

.add-label select,
input:focus {
  outline: none;
}

.label-input {
  margin-top: 10px;
  border: none;
  background-color: rgb(221, 215, 215);
  padding: 10px;
  border-radius: 10px;
}

.label-select {
  margin-top: 10px;
  padding: 10px;
  background-color: rgb(221, 215, 215);
  border-radius: 10px;
  border: none;
}

.save {
  background-color: rgb(67, 67, 219);
  color: #fff;
  padding: 10px;
  border-radius: 10px;
  border: none;
  margin-top: 25px;
  margin-bottom: 20px;
}

.save:hover {
  cursor: pointer;
  transform: scale(1.1);
}
